/* CAROUSEL */

.carousel {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.carousel__items {
  list-style: none;
  position: relative;
  width: 7690px;
  z-index: 0;
}

.carousel__item {
  display: inline-block;
  z-index: 0;
  position: relative;
}

.carousel__img {
  background-size: contain;
  z-index: 0;
  width: 100%;
  height: 100%;
}

.carousel__nav{
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 100;
}


/* BUTTONS */

.carousel__nav-btn {
  width: 10rem;
  height: 10rem;
  background-color: #eee;
  border: 0;
  cursor: pointer;
  padding: 0;
  outline: none;
}

.carousel__nav-btn svg {
  width: 5rem;
  height: 5rem;
}

.carousel__nav-btn:hover {
  background-color: #ccc;
}

.carousel__nav-btn:disabled {
  fill: #666;
  background-color: #eee;
  cursor: default;
}

.carousel__nav-btn:first-child {
  border-top-left-radius: 2px;
  border-right: 1px solid #666;
}


/* DOTS */

.carousel__dots {
  position: absolute;
  left: 5rem;
  bottom: 5rem;
  height: 10px;
  width: 8rem;
  display: -ms-flexbox;
  display: flex;
  z-index: 100;
}

.carousel__dot {
  height: 6px;
  width: 6px;
  background-color: #333;
  border-radius: 50%;
  display: inline-block;
}

.carousel__dot:not(:last-child) {
  margin-right: 1rem;
}



